.categories-section {
  background-color: white;
  margin: auto;
  margin-top: 10%;
  padding: 2rem 1.688rem 1.625rem 1.688rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 50%;

  h1 {
    font-family: "Roboto Slab", sans-serif;
    font-size: 2.5rem;
    font-weight: 500;
    color: #4386bf;
    text-align: center;
  }
}

button {
  display: flex;
  height: 2.813rem;
  margin: 0.5rem 1rem;
  padding: 1.188rem;
  border-radius: 3px;
  background-color: #0290ff;
  font-family: "Roboto Slab", sans-serif;
  font-size: 0.813rem;
  font-weight: bold;
  letter-spacing: 0.5px;
  justify-content: center;
  align-items: center;
  color: #fff;
  transition: 0.2s background-color linear;
  border: 0;

  &:hover {
    background-color: rgb(50, 50, 255);
  }

  &:active {
    background-color: rgb(50, 50, 255);
    box-shadow: inset 0 0 10px black;
  }
}
